import { createFeatureSelector, createSelector } from "@ngrx/store";
import { AuthState } from '../reducers/auth.reducer';

/* Permet de s√©lectionner une partie du state g√©n√©ral
Le auth state est dans le state g√©n√©ral */


// le auth match avec le nom dans le state g√©n√©ral. feature c'est parce qu'on est dnas le g√©n√©ral et on
// r√©cup un store
export const authSelector = createFeatureSelector('auth');
export const errorAuthSelector = createSelector(authSelector,
    (authState: AuthState) => {
        if (authState) {
            return authState.error;
        } else {
            return null;
        }

    }
);

export const authTokenSelector = createSelector(authSelector,
    (authState: AuthState) => {
        if (authState) {
            return authState.token;
        } else {
            return null;
        }
    });



export const isLoggedIn = createSelector(authSelector,
    (authState: AuthState) => {
        if (authState) {
            return authState.isLoggedIn;

        } else {
            return null;
        }
    });
    
export const currentUserSelector = createSelector(authSelector,
    (authState: AuthState) => {
        if (authState) {
            return authState.user;

        } else {
            return null;
        }
    });


    